.heading{
    margin: 60px 0;
}
.heading h1{
  width: max-content;
  position: relative;
  margin: auto;
}
.heading h1::before {
  position: absolute;
  content: "";
  width: 80%;
  height: 3px;
  background: #2196f3;
  bottom: -10px;
  margin: auto;
  left: 0;
  right: 0;
}
.review-box {
  width: 600px;
  background: #fff;
  margin: 0 auto;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 20px 5px #0000000f;
  text-align: center;
}
.review-box .image-sec {
  width: max-content;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  text-align: center;
}
.overlay {
  position: absolute;
  top: -6px;
  left: 6px;
  right: -25px;
  bottom: 0;
  border-radius: 50%;
  background: #009688;
  width: 120px;
  z-index: -1;
  height: 120px;
}
.quote-icon {
  width: 50px;
  height: 50px;
  background: #009688;
  color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  font-size: 24px;
  justify-content: center;
  position: absolute;
  top: 0;
  left: -10px;
}
.review-box .image-sec img {
  width: 120px;
  height: 120px;
  border-radius: 50%;
}
.person-name {
  margin-bottom: 4px;
  font-size: 18px;
}
.position {
  font-size: 14px;
  color: #2196f3;
}
.review p {
  font-style: italic;
  color: #5f5f5f;
  line-height: 1.7;
}
.controls svg {
  font-size: 20px;
  color: #2196f3;
  margin-right: 15px;
  margin-top: 15px;
  cursor: pointer;
}
.controls .review-btn {
  display: block;
  margin: 0 auto;
  margin-top: 20px;
  padding: 12px 20px;
  background: #2196f3;
  color: #fff;
  border: 1px solid #2196f3;
  font-size: 16px;
  font-weight: 600;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.2s;
}
.controls button:hover {
  background: #1c81d2;
  border: 1px solid #1c81d2;
}
button.prev,
button.next{
  border: none;
  background: 0;  
}
button.prev:hover,
button.next:hover{
  border: none;
  background: 0;  
}
button.prev:focus,
button.next:focus{
  outline: 0;  
}
